import { FC, useEffect, useState } from 'react';
import { Button, ButtonVariant, Input, SideModal } from '@src/components';
import * as ST from './styled';
import { useFormik } from 'formik';
import { handleFormError, toastError } from '@src/utils/toast';
import { UsersApi } from '@src/api/Users/UsersApi';
import { CmSelectValue, Select } from '@components/Select';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { RolesApi } from '@src/api/Roles/RolesApi';
import { Checkbox } from '@components/Checkbox';
import * as yup from 'yup';

interface IProps {
  onClose: () => void;
  refetch: () => void;
}

interface IForm {
  email: string,
  firstName: string,
  lastName: string,
  roles: SelectOption<string>[],
  password: string
}

let VALIDATION_SCHEMA = yup.object().shape({
  email: yup.string().required('Обязательное поле').email('Не соответствует формату email'),
  firstName: yup.string().required('Обязательное поле'),
  lastName: yup.string().required('Обязательное поле'),
  password: yup.string().required('Обязательное поле'),
});

export const CreateUserModal: FC<IProps> = ({ refetch, onClose }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isRolesLoading, setIsRolesLoading] = useState(true);
  const [roleOptions, setRoleOptions] = useState<SelectOption<string>[]>([]);

  const form = useFormik<IForm>({
    initialValues: {
      password: '',
      email: '',
      lastName: '',
      roles: [],
      firstName: '',
    },
    validationSchema: VALIDATION_SCHEMA,

    onSubmit: async (values) => {
      try {
        setIsLoading(true);
        await UsersApi.fetchCreateUser({ ...values, roles: values.roles.map(it => it.value)});

        refetch()
        onClose()
      } catch (e) {
        handleFormError(e, form);
      } finally {
        setIsLoading(false);
      }
    }
  });

  const loadRoles = async () => {
    try {
      setIsRolesLoading(true);
      const res = await RolesApi.fetchRoles();
      setRoleOptions(res.data.map(it => ({ value: it.id, label: it.value })));
    } catch (e) {
      handleFormError(e, form);
    } finally {
      setIsRolesLoading(false);
    }
  }

  const handleChangeRoles = (value: CmSelectValue) => {
    form.setFieldValue('roles', value);
  }

  useEffect(() => {
    loadRoles()
  }, []);

  return (
    <SideModal
      titleText="Создание пользователя"
      onClose={onClose}
      closeOnDocumentClick={false}
    >
      <ST.Form onSubmit={form.handleSubmit}>
        <Input
          autoComplete="email"
          name="email"
          onChange={form.handleChange}
          value={form.values.email}
          caption="Email"
          placeholder="Введите email"
          isError={Boolean(form.errors.email)}
          errorText={form.errors.email}
          onBlur={form.handleBlur}
        />
        <Input
          name="lastName"
          onChange={form.handleChange}
          value={form.values.lastName}
          caption="Фамилия"
          placeholder="Введите фамилию"
          isError={Boolean(form.errors.lastName)}
          errorText={form.errors.lastName}
          onBlur={form.handleBlur}
        />
        <Input
          name="firstName"
          onChange={form.handleChange}
          value={form.values.firstName}
          caption="Имя"
          placeholder="Введите имя"
          isError={Boolean(form.errors.firstName)}
          errorText={form.errors.firstName}
          onBlur={form.handleBlur}
        />
        <Input
          autoComplete="new-password"
          type="password"
          name="password"
          onChange={form.handleChange}
          value={form.values.password}
          caption="Пароль"
          placeholder="Введите пароль"
          isError={Boolean(form.errors.password)}
          errorText={form.errors.password}
          onBlur={form.handleBlur}
        />
        <Select
          placeholder="Выберите роли"
          caption="Роли"
          multi
          options={roleOptions}
          loading={isRolesLoading}
          value={form.values.roles}
          onChange={handleChangeRoles}
        />
        <ST.Footer>
          <Button text="Создать" fullWidth isLoading={isLoading} />
          <Button variant={ButtonVariant.OUTLINE} text="Отмена" fullWidth onClick={onClose} />
        </ST.Footer>
      </ST.Form>
    </SideModal>
  )
}
