import { FC } from 'react';
import { IUser } from '@src/api/Users/models';

import * as ST from './styled';
import { Avatar, Role, Status } from '@src/components';

interface IProps {
  user: IUser;
  onClick: () => void;
  refetch: () => void;
}

export const UserItem: FC<IProps> = ({ user, onClick }) => {
  return (
    <ST.Wrapper onClick={onClick}>
      <Avatar userId={user.id} src={user.avatar} firstName={user.firstName} lastName={user.lastName}   />
      <ST.Info>{user.lastName} {user.firstName} ({user.email})</ST.Info>
      <ST.Roles>
        {user.roles.map(it => <Role key={it.id} role={it.value} />)}
      </ST.Roles>
      <ST.StyledStatus status={user.status} />
    </ST.Wrapper>
  );
}
