import { useFormik } from 'formik';
import { useDebounce, useRoles, useUsers } from '@src/hooks';
import { ChangeEventHandler, useEffect, useMemo, useState } from 'react';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { CmSelectValue } from '@src/components';
import { IUser, UserStatus } from '@src/api/Users/models';

export interface IFilterPostsForm {
  roles: SelectOption<string>[];
  status: SelectOption<UserStatus>[];
}

export const useFilters = () => {
  const [activeUser, setActiveUser] = useState<IUser>();
  const [search, setSearch] = useState('');
  const debouncedSearch = useDebounce(search, 400)
  const { roles, getRoles, isLoading: isRolesLoading } = useRoles();
  const { getUsers, users, isLoading } = useUsers();

  const form = useFormik<IFilterPostsForm>({
    initialValues: {
      roles: [],
      status: [],
    },

    onSubmit: () => {}
  })

  useEffect(() => {
    getRoles();
  }, []);

  useEffect(() => {
    fetchUsers()
  }, [form.values.roles, debouncedSearch, form.values.status]);

  useEffect(() => {
    if (activeUser) {
      setActiveUser(users.find(it => it.id === activeUser.id))
    }
  }, [users]);

  const handleChangeSearch: ChangeEventHandler<HTMLInputElement> = (e) => {
    setSearch(e.target.value);
  }

  const fetchUsers = async () => {
    const { roles, status } = form.values;
    return await getUsers({ search: debouncedSearch, roles: roles.map(it => it.value), status: status.map(it => it.value) });
  }

  const rolesOptions: SelectOption<string>[] = useMemo(() => {
    return roles.map(it => ({ value: it.id, label: it.value }))
  }, [roles]);

  const statusOptions: SelectOption<UserStatus>[] = useMemo(() => {
    return Object.values(UserStatus).map(it => ({ value: it, label: it }))
  }, []);

  const createSelectChangeHandler = (field: string) => (value: CmSelectValue) => {
    form.setFieldValue(field, value);
  }

  const reset = () => {
    form.resetForm();
    setSearch('')
  }

  return {
    activeUser,
    setActiveUser,
    form,
    users,
    statusOptions,
    rolesOptions,
    createSelectChangeHandler,
    handleChangeSearch,
    search,
    isRolesLoading,
    isLoading,
    fetchUsers,
    reset,
  }
}
