import { ChangeEventHandler, FC, useEffect, useMemo, useState } from 'react';
import * as ST from './styled';
import { AppLoader, Button, PageHeader, Select } from '@src/components';
import { useDebounce, useModalControls, useUsers } from '@src/hooks';
import { IUser } from '@src/api/Users/models';
import { UserItem } from '@pages/Users/UserItem';
import { useRenderIfRole } from '@src/utils/roles';
import { RoleType } from '@src/api/Roles/models';
import { CreateUserModal } from '@pages/Users/CreateUserModal';
import { UserModal } from '@pages/Users/UserModal';
import { useFilters } from '@pages/Users/useFilters';

const Users: FC = () => {
  const {
    fetchUsers,
    users,
    isLoading,
    search,
    handleChangeSearch,
    form,
    createSelectChangeHandler,
    statusOptions,
    rolesOptions,
    isRolesLoading,
    activeUser,
    setActiveUser,
    reset,
  } = useFilters();
  const { isOpen, open, close } = useModalControls();
  const { execIf } = useRenderIfRole();

  const openUserModal = (user: IUser) => {
    setActiveUser(user);
  }

  const closeUserModal = () => {
    setActiveUser(undefined);
  }

  const renderedUsers = useMemo(() => {
    if (isLoading) {
      return (
        <AppLoader />
      )
    }

    if (!users.length) {
      return (
        <ST.EmptyText>
          Пользователей нет
        </ST.EmptyText>
      );
    }

    return (
      <ST.Content>
        {users.map(it => <UserItem key={it.id} user={it} refetch={fetchUsers} onClick={() => openUserModal(it)} />)}
      </ST.Content>
    )
  }, [users, isLoading]);

  return (
    <ST.Wrapper>
      <PageHeader
        title="Пользователи"
        search={search}
        count={`${users.length} пользователей`}
        onChangeSearch={handleChangeSearch}
        buttonText={execIf(RoleType.ADMIN, "Создать пользователя")}
        onButtonClick={execIf(RoleType.ADMIN, open)}
      />
      <ST.Filters>
        <Select value={form.values.roles} options={rolesOptions} loading={isRolesLoading} onChange={createSelectChangeHandler('roles')} placeholder="Выберите роли" multi searchable />
        <Select value={form.values.status} options={statusOptions} onChange={createSelectChangeHandler('status')} placeholder="Выберите статусы" multi />
        <Button text="Сбросить" onClick={reset} />
      </ST.Filters>
      {renderedUsers}
      {isOpen && (
        <CreateUserModal
          onClose={close}
          refetch={fetchUsers}
        />
      )}
      {activeUser && (
        <UserModal user={activeUser} onClose={closeUserModal} refetch={fetchUsers} />
      )}
    </ST.Wrapper>
  )
}

export default Users;
